.cards__results {
    width: 550px;
    height: 65px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.selects {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

.select__coisine,
.select__diet {
    background-color: #ffffff;
    color: #000000;
    width: 105px;
    font-family: inherit;
    font-size: inherit;
    font-weight: 600;
    cursor: pointer;
    padding: 7px;
    user-select: none;
}

.select__coisine {
    width: 110px;
}

@media (max-width: 600px) {
    .cards__results {
        width: 280px;
        height: 100px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}